:root {
  --main-bg-color: rgb(253, 249, 170);
  --main-text-color: rgba(0, 21, 189, 0.87);
}

html {
  display: block;
  width: 100%;
}

body {
  align-items: center;
  background-color: var(--main-bg-color);
  box-sizing: border-box;
  color: var(--main-text-color);
  display: flex;
  flex-flow: column;
  font-family: "Source Sans 3", sans-serif;
  font-size: 12px;
  justify-content: center;
  margin: 0 auto;
  padding: 0;
}

#container {
  box-sizing: border-box;
  counter-reset: item;
  display: flex;
  flex-flow: row wrap;
  margin: 18px auto 0;
  padding: 0;
  width: 100%;
}

#header-container {
  margin: 0 auto;
  padding: 0;
  width: 100%;
}

#header-container #header-row {
  align-items: flex-start;
  background: #4bebffd1;
  box-sizing: border-box;
  color: #272727;
  display: grid;
  font-family: monospace;
  font-weight: 200;
  grid-template-columns: 1fr 3fr 1fr 1fr 1fr;
  left: 0;
  list-style: none;
  margin: 0 auto;
  padding: 1px 2px;
  position: fixed;
  width: 100%;
}

#header-row button {
  background: transparent;
  border: transparent;
  cursor: pointer;
  font-family: monospace;
  font-size: 12px;
  padding: 0;
  text-align: left;
}

#header-row button.asc::after { content: " ↑";}

#header-row button.desc::after { content: " ↓";}

#header-row input[type="text"] {
  background: rgba(255, 255, 255, 0.8);
  border: 1px solid #272727;
  box-sizing: border-box;
  color: #272727;
  font-family: monospace;
  font-size: 12px;
  padding: 2px 4px;
  width: 100%;
}

#header-row input[type="text"]::placeholder {
  color: #666666;
  opacity: 0.7;
}

#container .row {
  align-items: flex-start;
  box-sizing: border-box;
  display: grid;
  grid-template-columns: 1fr 3fr 1fr 1fr 1fr;
  justify-content: flex-start;
  list-style: none;
  padding: 3px 2px;
  width: 100%;
}

#container .row:nth-child(even) {
  background-color: #efefef;
}

#container li::before {
  align-items: center;
  color: #3c3c3c3b;
  content: counter(item);
  counter-increment: item;
  display: flex;
  font-family: "Arial Narrow", Arial, serif;
  font-size: 1.5rem;
  justify-content: center;
  padding-left: 2px;
  position: absolute;
  width: 100%;
  z-index: -999;
}

#container .row span {
  -webkit-box-orient: vertical;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  overflow: hidden;
  text-overflow: ellipsis;
}

#container .row span[title="who"] a {
  font-weight: 500;
}

a:link:hover {
  text-decoration: none;
}
